<template>
  <tas-base-crud :config="config" list-card>
    <template v-slot:card-view="data">
      <div class="card-body p-5">
        <div class="row">
          <div class="col">
            <div class="d-flex align-items-center">
              <img v-img-fb="data.rowData.img_photo_created_by.tumbnail_url" class="symbol symbol-45 symbol-light mr-5 pointer" height="45px" alt="" @click="$children[0].$children[1].$emit('zoom',data.rowData.img_photo_created_by.tumbnail_url)">
              <div class="d-flex flex-column flex-grow-1">
                <a class="font-weight-bold text-dark-75 text-hover-primary font-size-lg mb-1 text-capitalize">{{ data.rowData.rel_created_by }}</a>
                <span class="text-muted font-weight-bold">{{ data.rowData.created_at | parse('longDateTime') }}</span>
              </div>
            </div>
          </div>
          <div class="col-auto">
            <button v-if="$_sys.isAllowed(config.permission.delete)" @click="$children[0].$children[1].deleteData(data.rowData)" class="btn btn-sm btn-icon btn-hover-danger">
              <i class="ri-delete-bin-7-line text-danger"></i>
            </button>
          </div>
        </div>
        <img v-img-fb="data.rowData.img_photo_cover? data.rowData.img_photo_cover.url : '/static/img/file/jpg.svg'" alt="" class="card-cover-img">
        <h5 class="card-title font-weight-bolder text-dark mb-1">{{ data.rowData.title }}</h5>
        <p class="text-line-3">{{ data.rowData.description }}</p>
        <div class="py-3">
          <div class="d-flex align-items-center justify-content-between mb-2">
            <span class="font-weight-bold mr-2 nowrap">Lokasi</span>
            <span class="">{{ data.rowData.place_of_event }}</span>
          </div>
          <div class="d-flex align-items-center justify-content-between mb-2">
            <span class="font-weight-bold mr-2 nowrap">Waktu Mulai</span>
            <span class="">{{ data.rowData.start_time | parse('longDateTime') }}</span>
          </div>
          <div class="d-flex align-items-center justify-content-between mb-2">
            <span class="font-weight-bold mr-2 nowrap">Waktu Berakhir</span>
            <span class="">{{ data.rowData.end_time | parse('longDateTime') }}</span>
          </div>
        </div>
        <hr>
        <button class="btn btn-info btn-block" @click="$children[0].$children[1].$emit('detail', data.rowData)">Lihat Detail</button>
        <button v-if="$_sys.isAllowed(config.permission.update)" @click="$children[0].$children[1].$emit('update', data.rowData)" class="btn btn-outline-danger btn-block">Update</button>
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
export default {
  name: 'crud-events',
  data () {
    return {
      config: {
        title: 'Event Perusahaan',
        model_api: 'events',
        getter: 'events',
        setter: 'events',
        custom_api: {
          list: 'custom/events'
        },
        pk_field: null,
        permission: {
          create: 'create-events',
          read: 'view-events',
          update: 'update-events',
          delete: 'delete-events'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'title', label: 'Judul Event' },
          { id: 'start_time', label: 'Waktu Mulai' },
          { id: 'end_time', label: 'Waktu Berakhir' },
          { id: 'place_of_event', label: 'Lokasi Event' },
          {
            id: 'child_data_event_documentations',
            label: 'File Dokumen',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'child_data_event_documentations.file_filename.url'
              },
              create: {
                type: 'file-multiple',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }, { max: 5 }]
              },
              update: {
                type: 'file-multiple',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }, { max: 5 }],
                view_data: 'child_data_event_documentations'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



